@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
p {
  font-family: "Roboto", sans-serif;
  font-size: 15px;
}

.disabled {
  cursor: not-allowed;
}
.text-white {
  color: white;
}
.menu-element:link,
.menu-element:visited,
.menu-element:hover,
.menu-element:active {
  text-decoration: none;
}

#navigation {
  padding: 10px;
  display: flex;
  grid-gap: 65px;
  gap: 65px;
}

#start {
  padding-left: 7%;
  padding-right: 7%;
  background-color: #595856;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
#logo {
  height: 16px;
  color: white;
  font-weight: bolder;
}

h1,
h2 {
  font-family: "Roboto", sans-serif;
}

#main {
  height: 100vh;
  padding-left: 7%;
  padding-right: 7%;
  background: url(/project-5-220127-react/static/media/dan-otis-OYFHT4X5isg-unsplash-imresizer.a1f2dcc4.jpg)
    center/100% 100% no-repeat;
  display: flex;
  flex-direction: column;
  justify-content: center;
  grid-gap: 25px;
  gap: 25px;
}
.slogan-main-1 {
  width: 50%;
  height: 68px;
  font-size: xx-large;
  font-weight: bolder;
  color: white;
}
.slogan-main-2 {
  width: 50%;
  height: 17px;
  font-size: medium;
  color: white;
  font-weight: bolder;
}
.button {
  width: 145px;
  height: 35px;
  border: 3px solid white;
  color: white;
  background-color: unset;
  transition-timing-function: linear;
  transition-duration: 1s;
}
.text-bolder {
  margin-top: unset;
  margin-bottom: unset;
  font-weight: bolder;
}
.button:hover {
  background-color: white;
  color: black;
}

.employees {
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.portrait {
  padding: 11%;
  margin: 2%;
  border-radius: 155px;
}
.img1 {
  background: url(/project-5-220127-react/static/media/birmingham-museums-trust-Kv1hYl9LlxU-unsplash-imresizer.15003570.jpg)
    center/contain no-repeat;
}
.img2 {
  background: url(/project-5-220127-react/static/media/birmingham-museums-trust-zTJX8ObJdp4-unsplash-imresizer.4a29e389.jpg)
    center/contain no-repeat;
}
.description {
  display: flex;
  flex-direction: column;
}
.credentials {
  font-weight: 1000;
}
.employees:nth-of-type(even) {
  flex-direction: row-reverse;
  text-align: right;
}

#about {
  height: 100vh;
  padding: 4% 7%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #f0f0f9;
}
.about-slogan {
  padding: 10px;
  font-size: xx-large;
  font-weight: bolder;
  color: #08073b;
}

.box-services {
  width: 100%;
  height: 100%;
  background-color: inherit;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.service {
  width: 75px;
  height: 75px;
  padding: 40px;
  margin: 4%;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  box-shadow: 3px 3px 6px -1px rgba(0, 0, 0, 0.82);
  transition-property: all;
  transition-timing-function: linear;
  transition-duration: 0.5s;
}
.text {
  text-align: center;
  font-weight: bolder;
  color: black;
}
.shape-design {
  width: 15px;
  height: 15px;
  position: absolute;
  left: -5px;
  top: -5px;
  border-radius: 171px;
  background-color: #f67e1d;
}
.service:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

#offer {
  padding: 4% 7%;
  background-color: #154172;
}
.slogan-offer {
  padding: 10px;
  font-size: xx-large;
  font-weight: bolder;
  color: white;
}

#contact {
  padding-left: 7%;
  padding-right: 7%;
}

#copyright {
  padding-left: 7%;
  padding-right: 7%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #595856;
}
.text-copyright {
  padding: 10px;
  font-weight: bolder;
  color: white;
}
.icons {
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
}

.App {
  width: 960px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

html {
  scroll-behavior: smooth;
  display: flex;
  justify-content: center;
}

